import {
  Sidebar,
  SidebarContent,
  SidebarFooter,
  SidebarGroup,
  SidebarGroupLabel,
  SidebarHeader,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  SidebarMenuSub,
  SidebarMenuSubButton,
  SidebarMenuSubItem,
  SidebarRail,
} from '@/_components/ui/sidebar';
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from '@/_components/ui/collapsible';
import { navItems } from '@/_constants/data';
import { ChevronRight, GalleryVerticalEnd } from 'lucide-react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import * as React from 'react';
import { Icons } from '../icons';

export const company = {
  name: 'Adriana ShowRoom',
  logo: GalleryVerticalEnd,
  plan: 'SampaioForce Softs',
};

export default function AppSidebar() {
  const pathname = usePathname();

  return (
    <Sidebar
      collapsible="icon"
      variant="inset"
      className="pt-2 bg-white dark:bg-zinc-900 border-r border-zinc-200 dark:border-zinc-800"
    >
      {/* Cabeçalho */}
      <SidebarHeader className="rounded-md shadow-sm mb-4 px-4">
        <div className="flex gap-3 items-center text-sidebar-accent-foreground">
          <div className="flex aspect-square size-10 items-center justify-center rounded-lg bg-indigo-600 text-white">
            <company.logo className="size-5" />
          </div>
          <div className="grid flex-1 text-left text-sm leading-tight">
            <span className="truncate font-semibold">{company.name}</span>
            <span className="truncate text-xs text-muted-foreground">
              {company.plan}
            </span>
          </div>
        </div>
      </SidebarHeader>

      {/* Conteúdo */}
      <SidebarContent className="overflow-x-hidden flex-col px-2">
        <SidebarGroup>
          <SidebarGroupLabel className="text-xs font-medium text-zinc-500 uppercase tracking-wide mb-2 px-2">
            Painel de Navegação
          </SidebarGroupLabel>
          <SidebarMenu className="rounded-md space-y-1">
            {navItems.map((item) => {
              const Icon = item.icon ? Icons[item.icon] : Icons.logo;
              return item?.items && item?.items?.length > 0 ? (
                <Collapsible
                  key={item.title}
                  asChild
                  defaultOpen={item.isActive}
                  className="group/collapsible"
                >
                  <SidebarMenuItem>
                    <CollapsibleTrigger asChild>
                      <SidebarMenuButton
                        tooltip={item.title}
                        isActive={pathname === item.url}
                        className="flex items-center gap-3 px-3 py-2 text-sm font-medium text-zinc-700 dark:text-zinc-200 hover:bg-zinc-100 dark:hover:bg-zinc-800 transition rounded-lg w-full"
                      >
                        {item.icon && <Icon className="size-4" />}
                        <span className="flex-1">{item.title}</span>
                        <ChevronRight className="ml-auto size-4 transition-transform duration-200 group-data-[state=open]/collapsible:rotate-90" />
                      </SidebarMenuButton>
                    </CollapsibleTrigger>
                    <CollapsibleContent>
                      <SidebarMenuSub className="ml-6 border-l border-zinc-200 dark:border-zinc-700 pl-4 space-y-1 mt-1">
                        {item.items?.map((subItem) => (
                          <SidebarMenuSubItem key={subItem.title}>
                            <SidebarMenuSubButton
                              asChild
                              isActive={pathname === subItem.url}
                              className="text-sm text-zinc-600 dark:text-zinc-300 hover:text-zinc-900 dark:hover:text-white transition"
                            >
                              <Link href={subItem.url}>
                                {subItem.title}
                              </Link>
                            </SidebarMenuSubButton>
                          </SidebarMenuSubItem>
                        ))}
                      </SidebarMenuSub>
                    </CollapsibleContent>
                  </SidebarMenuItem>
                </Collapsible>
              ) : (
                <SidebarMenuItem key={item.title}>
                  <SidebarMenuButton
                    asChild
                    tooltip={item.title}
                    isActive={pathname === item.url}
                    className="flex items-center gap-3 px-3 py-2 text-sm font-medium text-zinc-700 dark:text-zinc-200 hover:bg-zinc-100 dark:hover:bg-zinc-800 transition rounded-lg w-full"
                  >
                    <Link href={item.url}>
                      <Icon className="size-4" />
                      <span>{item.title}</span>
                    </Link>
                  </SidebarMenuButton>
                </SidebarMenuItem>
              );
            })}
          </SidebarMenu>
        </SidebarGroup>
      </SidebarContent>

      {/* Footer */}
      <SidebarFooter className="border-t border-zinc-200 dark:border-zinc-800 px-4 py-3">
        <div className="flex items-center gap-3 text-sm text-muted-foreground">
          <div className="flex h-8 w-8 items-center justify-center rounded-md bg-indigo-600 text-white">
            <company.logo className="size-4" />
          </div>
          <div className="flex flex-col leading-tight">
            <span className="font-medium text-xs">{company.name}</span>
            <span className="text-[10px]">{company.plan}</span>
          </div>
        </div>
      </SidebarFooter>

      {/* Rail (modo compacto) */}
      <SidebarRail />
    </Sidebar>
  );
}
